import React, { useState, useEffect } from "react";
import api from "../api/axios";
import Map from "../components/Map";

const Register = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");

  //  
  const [interestTags, setInterestTags] = useState([]);
  const [newTag, setNewTag] = useState("");

  // 
  const [latitude, setLatitude] = useState(null);
  const [longitude, setLongitude] = useState(null);

  //     
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (pos) => {
          setLatitude(pos.coords.latitude);
          setLongitude(pos.coords.longitude);
        },
        (err) => {
          console.error(err);
          alert("    .");
        }
      );
    }
  }, []);

  //  
  const handleAddTag = () => {
    if (newTag && !interestTags.includes(newTag)) {
      setInterestTags([...interestTags, newTag]);
    }
    setNewTag("");
  };

  //  
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!username || !password || !email || !name) {
      alert("  .");
      return;
    }

    try {
      const res = await api.post("/auth/register", {
        username,
        password,
        email,
        name,
        interest_tags: interestTags,
        latitude,
        longitude,
      });

      console.log(" :", res.data);
      alert(" !");
      window.location.href = "/login";
    } catch (err) {
      console.error(err);
      alert(" !    .");
    }
  };

  return (
    <div className="container mt-4">
      <h2></h2>
      <form onSubmit={handleSubmit}>
        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input
            type="text"
            className="form-control"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>

        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input
            type="password"
            className="form-control"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>

        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input
            type="email"
            className="form-control"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>

        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input
            type="text"
            className="form-control"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
        </div>

        {/*   */}
        <div className="mb-3">
          <label className="form-label"> </label>
          <div>
            {interestTags.map((tag, i) => (
              <span key={i} className="badge bg-primary me-2">
                {tag}
              </span>
            ))}
          </div>
          <div className="input-group mt-2">
            <input
              type="text"
              className="form-control"
              placeholder="  "
              value={newTag}
              onChange={(e) => setNewTag(e.target.value)}
            />
            <button type="button" className="btn btn-outline-secondary" onClick={handleAddTag}>
              
            </button>
          </div>
        </div>

        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <p>
            {latitude && longitude
              ? `(${latitude.toFixed(4)}, ${longitude.toFixed(4)})`
              : "   ..."}
          </p>
          {latitude && longitude && (
            <Map
              latitude={latitude}
              longitude={longitude}
              onClick={(lat, lng) => {
                setLatitude(lat);
                setLongitude(lng);
              }}
            />
          )}
          <small className="text-muted">     .</small>
        </div>

        <button type="submit" className="btn btn-primary mt-3">
          
        </button>
      </form>
    </div>
  );
};

export default Register;
