import React, { useEffect, useRef } from "react";
import Chart from "chart.js/auto";

const UserServiceDashboard = () => {
  //  
  const dummyTop5Groups = [
    { id: 1, title: "Java ", distance: 0.8, category: "", lat: 37.449613, lng: 127.127877 },
    { id: 2, title: "AI ", distance: 1.2, category: "AI/ML", lat: 37.450908, lng: 127.126498 },
    { id: 3, title: " ", distance: 2.1, category: "", lat: 37.448834, lng: 127.130092 },
    { id: 4, title: "Spring Boot ", distance: 3.5, category: "", lat: 37.447500, lng: 127.128500 },
    { id: 5, title: " ", distance: 4.8, category: "", lat: 37.446200, lng: 127.125000 },
  ];

  const dummyScores = [
    { title: "Java ", distanceScore: 40, interestScore: 30 },
    { title: "AI ", distanceScore: 35, interestScore: 40 },
    { title: " ", distanceScore: 25, interestScore: 50 },
  ];

  const dummyCategoryDist = {
    "": 3,
    "AI/ML": 2,
    "": 2,
    "": 1,
  };

  const dummyDistanceDist = {
    "0~1km": 2,
    "1~3km": 3,
    "3~5km": 2,
  };

  // / 
  useEffect(() => {
    //   (Pie)
    const ctx1 = document.getElementById("categoryChart");
    if (ctx1) {
      new Chart(ctx1, {
        type: "pie",
        data: {
          labels: Object.keys(dummyCategoryDist),
          datasets: [
            {
              data: Object.values(dummyCategoryDist),
              backgroundColor: ["#0d6efd", "#198754", "#ffc107", "#dc3545"],
            },
          ],
        },
      });
    }

    //   (Bar)
    const ctx2 = document.getElementById("distanceChart");
    if (ctx2) {
      new Chart(ctx2, {
        type: "bar",
        data: {
          labels: Object.keys(dummyDistanceDist),
          datasets: [
            {
              label: " ",
              data: Object.values(dummyDistanceDist),
              backgroundColor: "#6610f2",
            },
          ],
        },
      });
    }
  }, []);

  //  
  const mapRef = useRef(null);

  useEffect(() => {
    if (window.kakao && window.kakao.maps) {
      const container = document.getElementById("serviceMap");
      const options = {
        center: new window.kakao.maps.LatLng(37.449613, 127.127877),
        level: 5,
      };
      const map = new window.kakao.maps.Map(container, options);
      mapRef.current = map;

      //  
      dummyTop5Groups.forEach((g) => {
        const marker = new window.kakao.maps.Marker({
          position: new window.kakao.maps.LatLng(g.lat, g.lng),
          map: map,
        });

        const infowindow = new window.kakao.maps.InfoWindow({
          content: `<div style="padding:5px;font-size:12px;">${g.title}</div>`,
        });

        window.kakao.maps.event.addListener(marker, "click", () => {
          infowindow.open(map, marker);
        });
      });
    }
  }, []);

  return (
    <div className="container mb-4">
      <h2 className="dashboard-title text-center my-4">  </h2>

      {/* Top 5  */}
      <div className="card mb-4">
        <div className="card-header bg-primary text-white">    Top 5</div>
        <div className="card-body">
          <ul className="list-group">
            {dummyTop5Groups.map((g) => (
              <li key={g.id} className="list-group-item d-flex justify-content-between align-items-center">
                <div>
                  <div className="fw-semibold">{g.title}</div>
                  <div className="text-muted small">
                    {g.category}  {g.distance} km
                  </div>
                </div>
                <button className="btn btn-sm btn-outline-primary">/ </button>
              </li>
            ))}
          </ul>
        </div>
      </div>

      {/*   */}
      <div className="card mb-4">
        <div className="card-header bg-success text-white">   ( + )</div>
        <div className="card-body">
          <ul className="list-group">
            {dummyScores.map((s, idx) => (
              <li key={idx} className="list-group-item">
                <strong>{s.title}</strong>
                <div className="small text-muted">
                   : {s.distanceScore} /  : {s.interestScore} / :{" "}
                  {s.distanceScore + s.interestScore}
                </div>
              </li>
            ))}
          </ul>
        </div>
      </div>

      {/*   +   */}
      <div className="row g-4 mb-4">
        <div className="col-md-6">
          <div className="card">
            <div className="card-header bg-warning">    </div>
            <div className="card-body">
              <canvas id="categoryChart" height="300"></canvas>
            </div>
          </div>
        </div>

        <div className="col-md-6">
          <div className="card">
            <div className="card-header bg-info">   </div>
            <div className="card-body">
              <canvas id="distanceChart" height="300"></canvas>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="card">
        <div className="card-header bg-dark text-white">   </div>
        <div className="card-body">
          <div id="serviceMap" style={{ width: "100%", height: "400px" }}></div>
        </div>
      </div>
    </div>
  );
};

export default UserServiceDashboard;
