// src/pages/main/RecommendGroups.jsx
import React, { useState } from "react";
import axios from "axios";

const RecommendGroups = ({ onAddSchedule }) => {
  const [groups, setGroups] = useState([]); //    
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  //     
  const fetchRecommendedGroups = () => {
    setLoading(true);
    setError(null);
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        async (pos) => {
          try {
            const lat = pos.coords.latitude;
            const lng = pos.coords.longitude;
            console.log("userLat:", lat, "userLon:", lng);
            //  API  (: GET /api/study-groups/recommend?lat=...&lng=...)
            const interestTags = "Java,Spring";
            const res = await axios.get("http://localhost:8080/api/study-groups/recommend", {
              params: { userLat: lat, userLon: lng, interestTags }, //interestTags 
            });
            setGroups(res.data); // API   
          } catch (err) {
             if (err.response) {
    //       (404, 500 )
    console.log("status:", err.response.status);
    console.log("data:", err.response.data);
  } else if (err.request) {
    //      
    console.log("No response received:", err.request);
  } else {
    //     (CORS )
    console.log("Error", err.message);
  }

  setError("     .");
}
 finally {
            setLoading(false);
          }
        },
        (err) => {
          console.error(err);
          setError("    .");
          setLoading(false);
        }
      );
    } else {
      setError("    .");
    }
  };

  return (
    <div>
      <h3> </h3>
      <button className="btn btn-primary mb-3" onClick={fetchRecommendedGroups}>
             
      </button>

      {loading && <p> ...</p>}
      {error && <p className="text-danger">{error}</p>}

      <div className="row">
        {groups.length > 0 &&
          groups.map((g) => (
            <div key={g.groupId} className="col-md-6 mb-3">
              <div className="card border-success shadow-sm">
                <div className="card-body">
                  <h5 className="card-title">{g.title}</h5>
                  <p className="card-text">
                    : {g.category} <br />
                    : {g.distance.toFixed(1)} km
                  </p>
                  <button
                    className="btn btn-success btn-sm"
                    onClick={() =>
                      onAddSchedule({
                        title: g.title,
                        leader: "",
                        date: new Date(), //      
                        location: g.category,
                        content: `: ${g.distance.toFixed(1)} km`,
                        lat: g.latitude,
                        lng: g.longitude,
                        members: 1,
                        max: 10,
                      })
                    }
                  >
                    / 
                  </button>
                </div>
              </div>
            </div>
          ))}
      </div>
    </div>
  );
};

export default RecommendGroups;
