import React, { useState } from "react";
// import React, { useState, useEffect } from "react";
// import api from "../api/axios";
// -> API 
import Map from "../components/Map";

const Profile = () => {
  const [profile, setProfile] = useState({
    username: "testuser",
    name: "",
    email: "test@example.com",
    interestTags: ["Java", "AI"],
    latitude: null,
    longitude: null,
  });

  //   
  const handleGetLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (pos) => {
          setProfile((prev) => ({
            ...prev,
            latitude: pos.coords.latitude,
            longitude: pos.coords.longitude,
          }));
        },
        (err) => {
          console.error(err);
          alert("    .");
        }
      );
    }
  };

  //   
  const [newTag, setNewTag] = useState("");
  const handleAddTag = () => {
    if (newTag && !profile.interestTags.includes(newTag)) {
      setProfile((prev) => ({
        ...prev,
        interestTags: [...prev.interestTags, newTag],
      }));
    }
    setNewTag("");
  };

  //  (  )
  const handleSave = async () => {
    try {
      //  console.log 
      console.log(" :", profile);

      //  API  :
      // await api.put("/users/me", profile);

      alert(" !");
    } catch (err) {
      console.error(err);
      alert(" !");
    }
  };

  return (
    <div className="container mt-4">
      <h2> </h2>

      <div className="mb-3">
        <label className="form-label"></label>
        <input type="text" className="form-control" value={profile.username} disabled />
      </div>

      <div className="mb-3">
        <label className="form-label"></label>
        <input
          type="text"
          className="form-control"
          value={profile.name}
          onChange={(e) => setProfile({ ...profile, name: e.target.value })}
        />
      </div>

      <div className="mb-3">
        <label className="form-label"></label>
        <input
          type="email"
          className="form-control"
          value={profile.email}
          onChange={(e) => setProfile({ ...profile, email: e.target.value })}
        />
      </div>

      <div className="mb-3">
        <label className="form-label"> </label>
        <div>
          {profile.interestTags.map((tag, i) => (
            <span key={i} className="badge bg-primary me-2">
              {tag}
            </span>
          ))}
        </div>
        <div className="input-group mt-2">
          <input
            type="text"
            className="form-control"
            placeholder="  "
            value={newTag}
            onChange={(e) => setNewTag(e.target.value)}
          />
          <button className="btn btn-outline-secondary" onClick={handleAddTag}>
            
          </button>
        </div>
      </div>

      <div className="mb-3">
        <label className="form-label"></label>
        <p>
          {profile.latitude && profile.longitude
            ? `(${profile.latitude.toFixed(4)}, ${profile.longitude.toFixed(4)})`
            : "  ."}
        </p>
        <button className="btn btn-sm btn-success" onClick={handleGetLocation}>
            
        </button>
      </div>

        {profile.latitude && profile.longitude && (
            <div className="mt-3">
            <Map latitude={profile.latitude} longitude={profile.longitude} />
            </div>
        )}

      <button className="btn btn-primary mt-3" onClick={handleSave}>
        
      </button>
    </div>
  );
};

export default Profile;
