// src/pages/admin/UserList.jsx
import React, { useState } from "react";

const dummyUsers = [
  { id: 1, username: "testuser", name: "", email: "hong@test.com", role: "USER" },
  { id: 2, username: "admin", name: "", email: "admin@test.com", role: "ADMIN" },
];

const UserList = () => {
  const [users, setUsers] = useState(dummyUsers);

  const handleDelete = (id) => {
    if (window.confirm(" ?")) {
      setUsers(users.filter((u) => u.id !== id));
    }
  };

  return (
    <div>
      <h2>  </h2>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {users.map((u) => (
            <tr key={u.id}>
              <td>{u.id}</td>
              <td>{u.username}</td>
              <td>{u.name}</td>
              <td>{u.email}</td>
              <td>{u.role}</td>
              <td>
                <button className="btn btn-danger btn-sm" onClick={() => handleDelete(u.id)}>
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default UserList;
